import React from 'react';
import Card from './Card';

const CardList = ({robots}) => (
    <div>
        {robots.map((robot, i) => ( 
            <Card 
                key = {robots[i].id}
                id = {robots[i].id}
                name = {robots[i].name}
                email = {robots[i].email}
            /> 
        ))}
    </div>
);

export default CardList;